import { ref } from "@vue/reactivity";
// import { Popup } from 'vant';

export default {
  setup() {
    //筛选待支付
    let mabytopay = ref("");
    //准备发货
    let readytopay = ref("");
    //待评价
    let talkabout = ref("");
    //是否展示
    let showhave = ref(false);

    //点击取消返回当前页面
    let test = () => {
      show.value = false;
      show2.value = false;
    };
    //弹出层
    const show = ref(false);
    const show2 = ref(false);

    //点击删除订单//获取点击的id
    let gonum = ref(0);
    const showPopup = id => {
      show.value = true;
      console.log(id);
      gonum.value = id;
    };
    //点击确认收货或确认付款时候触发
    const showPopup2 = id => {
      show2.value = true;
      console.log(id);
      gonum.value = id;
    };
    //获取所有订单的数据
    let allorder = ref(JSON.parse(window.localStorage.getItem("allorder")));
    if (allorder.value == null || allorder.value.length == 0) {
      showhave.value = true;
    } else {
      showhave.value = false;
    }
    const activeName = ref("a");
    //修改类型
    let changetype = type => {
      console.log(type);
      //查找点击的下标
      let index = allorder.value.findIndex(item => {
        return item.type == type;
      });
      allorder.value[index].type = type + 1;
      // 存入本地
      console.log(allorder.value);
      window.localStorage.setItem("allorder", JSON.stringify(allorder.value));
      //未支付菜单
      mabytopay.value = allorder.value.filter(item => {
        return item.type == 1;
      });
      //准备发货支付菜单
      readytopay.value = allorder.value.filter(item => {
        return item.type == 2;
      });
      //待评价菜单
      talkabout.value = allorder.value.filter(item => {
        return item.type == 3;
      });
      console.log(talkabout.value, 111);
      if (mabytopay.value.length == 0 || mabytopay.value == null) {
        youlook.value = false;
      } else {
        youlook.value = true;
      }
      if (readytopay.value.length == 0 || readytopay.value == null) {
        theylook.value = false;
      } else {
        theylook.value = true;
      }
      if (talkabout.value.length == 0 || talkabout.value == null) {
        ourlook.value = false;
      } else {
        ourlook.value = true;
      }
      show2.value = false;
    };

    //未支付菜单
    mabytopay.value = allorder.value.filter(item => {
      return item.type == 1;
    });
    let youlook = ref(false);
    if (mabytopay.value.length == 0 || mabytopay.value == null) {
      youlook.value = false;
    } else {
      youlook.value = true;
    }
    //准备发货支付菜单
    readytopay.value = allorder.value.filter(item => {
      return item.type == 2;
    });
    let theylook = ref(false);
    if (readytopay.value.length == 0 || readytopay.value == null) {
      theylook.value = false;
    } else {
      theylook.value = true;
    }
    //准备发货支付菜单
    talkabout.value = allorder.value.filter(item => {
      return item.type == 3;
    });
    let ourlook = ref(false);
    if (talkabout.value.length == 0 || talkabout.value == null) {
      ourlook.value = false;
    } else {
      ourlook.value = true;
    }

    //删除订单
    let yesdel = id => {
      //查找id下标
      let index = allorder.value.findIndex(item => {
        return item.id == id;
      });
      console.log(allorder.value[index]);
      allorder.value.splice(index, 1);
      window.localStorage.setItem("allorder", JSON.stringify(allorder.value));
